.weather-panel {
  position: absolute;
  top: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.weather-dashboard {
  display: flex;
  min-width: 50%;
  min-height: 50%;
  justify-content: space-evenly;
  align-items: center;
  color: white;
  background-color: rgba(33,33,33,0.4);
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.weather-left {
  padding: 15px;
  margin: 5%;
}
.weather-info {
  /* border-left: 1px solid black; */
  padding: 15px;
  margin: 5%;
}
.weather-info input{
  padding: 5px 7px;
  border-radius: 50px;
  border: 2px solid white;
  margin: 0 5px 5px 0;
  background-color: rgba(255,255,255,0);
  color: white;
  text-transform: capitalize;
}
.weather-info input:focus{
  border: 2px solid #c1fffa;
}
.weather-info button{
  padding: 5px 7px;
  border-radius: 50px;
  border: 2px solid white;
  background-color: rgba(255,255,255,0);
  color: white;
}
.weather-info button:hover{
  border: 2px solid black;
  background-color: rgba(255,255,255,1);
  color: black;
  box-shadow: 0 8px 12px rgba(0,0,0,0.25), 0 5px 5px rgba(0,0,0,0.22);
}

@media only screen and (max-width: 780px) {
  .weather-dashboard {
    flex-direction: column;
  }
}
