.weather-icon i {
  font-size: 110pt;
}

h1.location-name {
  font-size: 200%;
  font-family: 'Open Sans', sans-serif;
  margin: 13px 0;
}

h5.info-item {
  margin: 7px 0;
  font-size: 110%;
  font-family: 'Lato', sans-serif;
  text-transform: lowercase;
  color: white;
}

h3.subHeading {
  font-size: 130%;
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
  font-family: 'Open Sans', sans-serif;
  padding: 0 0 10px 0;
  border-bottom: 1px solid white;
}

.weather-panel {
  position: absolute;
  top: 0;
  height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}
.weather-dashboard {
  display: -webkit-flex;
  display: flex;
  min-width: 50%;
  min-height: 50%;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  background-color: rgba(33,33,33,0.4);
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.weather-left {
  padding: 15px;
  margin: 5%;
}
.weather-info {
  /* border-left: 1px solid black; */
  padding: 15px;
  margin: 5%;
}
.weather-info input{
  padding: 5px 7px;
  border-radius: 50px;
  border: 2px solid white;
  margin: 0 5px 5px 0;
  background-color: rgba(255,255,255,0);
  color: white;
  text-transform: capitalize;
}
.weather-info input:focus{
  border: 2px solid #c1fffa;
}
.weather-info button{
  padding: 5px 7px;
  border-radius: 50px;
  border: 2px solid white;
  background-color: rgba(255,255,255,0);
  color: white;
}
.weather-info button:hover{
  border: 2px solid black;
  background-color: rgba(255,255,255,1);
  color: black;
  box-shadow: 0 8px 12px rgba(0,0,0,0.25), 0 5px 5px rgba(0,0,0,0.22);
}

@media only screen and (max-width: 780px) {
  .weather-dashboard {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}

@keyframes background-movement {
			from { background-position: 0 0; }
			to { background-position: 100% 5%; -webkit-transform: scale(1.1); transform: scale(1.1); }
		}
    @-webkit-keyframes background-movement {
			from { background-position: 0 0; }
			to { background-position: 100% 5%; -webkit-transform: scale(1.1); transform: scale(1.1); }
		}

.background {
  height: 100vh;
  width: 100%;
  /* background-position: center; */
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-animation: background-movement 21s linear infinite;
          animation: background-movement 21s linear infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  transition:           0.7s ease-in-out;
}
