@keyframes background-movement {
			from { background-position: 0 0; }
			to { background-position: 100% 5%; transform: scale(1.1); }
		}
    @-webkit-keyframes background-movement {
			from { background-position: 0 0; }
			to { background-position: 100% 5%; transform: scale(1.1); }
		}
		@-ms-keyframes background-movement {
			from { background-position: 0 0; }
			to { background-position: 100% 5%; transform: scale(1.1); }
		}
		@-moz-keyframes background-movement {
			from { background-position: 0 0; }
			to { background-position: 100% 5%; transform: scale(1.1); }
		}

.background {
  height: 100vh;
  width: 100%;
  /* background-position: center; */
  background-repeat: no-repeat;
  background-size: cover;
  animation: background-movement 21s linear infinite;
  animation-direction: alternate;
  -webkit-transition:   0.7s ease-in-out;
  -moz-transition:      0.7s ease-in-out;
  -o-transition:        0.7s ease-in-out;
  transition:           0.7s ease-in-out;
}
body {
  overflow-x: hidden;
  overflow-y: hidden;
}
